.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity .2s linear;
  font-family: 'inter', Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}